import * as React from 'react';
import { Meta } from '@storybook/react';
import styled from 'styled-components';
import * as ColumnsSvg from './columns';
import * as IconsSvg from './index';

export default {
  title: 'Atoms/Icons',
  argTypes: {},
} as Meta;

const Wrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

const WrapperColumn = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
`;

const Item = styled.div`
  cursor: default;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 2px solid #e4e7f2;
  border-radius: 4px;
  padding: 15px;
  width: 150px;
  margin: 10px;
`;

const ItemColumn = styled.div`
  margin: 10px;
`;

const Label = styled.h2`
  font-size: 13px;
  margin-top: 10px;
  color: #53649f;
  font-weight: normal;
`;

export const NormalIcons = () => {
  const keys = Object.keys(IconsSvg) as IconType[];

  return (
    <Wrapper>
      {keys
        .filter((o) => !o.startsWith('Column'))
        .map((iconType: IconType) => {
          const Component = IconsSvg[iconType];

          if (!Component) {
            return null;
          }

          return (
            <Item key={iconType}>
              <Component width={24} height={24} />
              <Label>{iconType}</Label>
            </Item>
          );
        })}
    </Wrapper>
  );
};

export const ColumnIcons = () => {
  const keys = Object.keys(ColumnsSvg) as ColumnType[];

  return (
    <WrapperColumn>
      {keys.map((iconType: ColumnType) => {
        const Component = ColumnsSvg[iconType];

        if (!Component) {
          return null;
        }

        return (
          <ItemColumn title={iconType} style={{ width: 320 }}>
            <Component />
          </ItemColumn>
        );
      })}
    </WrapperColumn>
  );
};
