import React, { useEffect, useState } from 'react';
import { Button } from 'antd';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';
import Icon from '../../../atoms/icons/Icon';

export const StyledButtons = styled.div`
  display: flex;
  align-items: center;

  > button {
    line-height: 32px;

    &[data-hide='desktop'] {
      border-radius: 4px 0 0 4px;
    }
    &[data-hide='mobile'] {
      border-radius: 0 4px 4px 0;
    }

    > svg {
      pointer-events: none;
    }
  }

  > svg {
    opacity: 0.6;
    margin-right: 5px;

    &:hover {
      opacity: 1;
    }
  }
`;

export type TypeHideOnValue = {
  hideContentOnDesktop: string;
  hideContentOnMobile: string;
};

export const defaultHideOnValue: TypeHideOnValue = {
  hideContentOnDesktop: 'false',
  hideContentOnMobile: 'false',
};

function WidgetContentHideOn({ value: valueProp, initValue, onChange }: WidgetProps<TypeHideOnValue>) {
  const [value, setValue] = useState(() => {
    return initValue
      ? {
          hideContentOnDesktop: initValue?.hideContentOnDesktop || 'false',
          hideContentOnMobile: initValue?.hideContentOnMobile || 'false',
        }
      : defaultHideOnValue;
  });

  useEffect(() => {
    if (valueProp) {
      setValue(valueProp);
    }
  }, [valueProp]);

  function handleClick(evt: any) {
    const dataHide = evt.target.getAttribute('data-hide');
    const newValue = {
      hideContentOnDesktop: String(dataHide === 'desktop'),
      hideContentOnMobile: String(dataHide === 'mobile'),
    };
    setValue(newValue);
    onChange(WidgetPath.ContentHideOn, newValue);
  }

  function handleClear() {
    const newValue = {
      hideContentOnDesktop: 'false',
      hideContentOnMobile: 'false',
    };
    setValue(newValue);
    onChange(WidgetPath.ContentHideOn, newValue);
  }

  const showClear = value.hideContentOnDesktop === 'true' || value.hideContentOnMobile === 'true';

  return (
    <WidgetWrap>
      <label>Row Hide on</label>
      <StyledButtons>
        {showClear && <Icon name="Clear" onClick={handleClear} />}
        <Button
          data-hide="desktop"
          onClick={handleClick}
          type={value.hideContentOnDesktop === 'true' ? 'primary' : undefined}
          icon={<Icon name="Desktop" />}
        />
        <Button
          data-hide="mobile" //
          onClick={handleClick}
          type={value.hideContentOnMobile === 'true' ? 'primary' : undefined}
          icon={<Icon name="Mobile" />}
        />
      </StyledButtons>
    </WidgetWrap>
  );
}

export { WidgetContentHideOn };
