import Icon from '../../icons/Icon';
import React, { useEffect, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';

export const StyledIcons = styled.div`
  display: flex;
  gap: 12px;

  > svg {
    opacity: 0.5;
    cursor: pointer;

    * {
      pointer-events: none;
    }

    &.active {
      opacity: 1;
    }
  }
`;

function WidgetTextAlignOuter({ value: valueProp, initValue, onChange }: WidgetProps<string>) {
  const [value, setValue] = useState(initValue);

  useEffect(() => {
    if (valueProp) {
      setValue(valueProp);
    }
  }, [valueProp]);

  function handleClick(evt: any) {
    const dataAlign: string = evt.target.getAttribute('data-align');
    setValue(dataAlign);
    onChange(WidgetPath.TextAlignOuter, dataAlign);
  }

  return (
    <WidgetWrap>
      <label>Align</label>
      <StyledIcons>
        <Icon
          onClick={handleClick} //
          name="TextAlignLeft"
          fill="#898989"
          className={value === 'left' ? 'active' : ''}
          data-align="left"
        />
        <Icon
          onClick={handleClick}
          name="TextAlignCenter"
          fill="#898989"
          className={value === 'center' ? 'active' : ''}
          data-align="center"
        />
        <Icon
          onClick={handleClick} //
          name="TextAlignRight"
          fill="#898989"
          className={value === 'right' ? 'active' : ''}
          data-align="right"
        />
      </StyledIcons>
    </WidgetWrap>
  );
}

export { WidgetTextAlignOuter };
