import React, { useEffect, useState, Fragment } from 'react';
import { Input, Select } from 'antd';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';
import { WidgetSectionTitle } from '../../../atoms/widgetSectionTitle';

export const StyledInputWrap = styled.div`
  width: 100%;

  label + & {
    margin-top: 5px;
  }

  .ant-input {
    padding-top: 2px;
    padding-bottom: 2px;
  }

  .ant-select + &,
  .ant-input-group-wrapper + .ant-input-group-wrapper {
    margin-top: 10px;
  }

  .ant-input-group-addon {
    font-size: 12px;

    & + input {
      font-size: 12px;
    }
  }

  .ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    height: 30px;
  }
  .ant-select-single .ant-select-selector .ant-select-selection-item,
  .ant-select-single .ant-select-selector .ant-select-selection-placeholder {
    line-height: 28px;
  }
`;

export enum WidgetActionType {
  open_web_page = 'open_web_page', // https://...
  send_email = 'send_email', // "mailto:test@gmail.com?subject=ahihi&body=body";
  make_call = 'make_call', // tel:0343261825
  send_sms = 'send_sms', // sms:0343261825?message=message_ne;
}

export type WidgetValuePropActionLink = {
  href: string;
};
type WidgetValueProp = {
  src: string;
  alt: string;
} & WidgetValuePropActionLink;

export type WidgetValueStateActionLink = {
  urlOrMailtoOrTel: string; // Url, Mail To, Tel
  subject: string;
  bodyOrMessage: string;
  type: WidgetActionType;
};
type WidgetValueState = {
  src: string;
  alt: string;
} & WidgetValueStateActionLink;

export const defaultValuePropActionLink = {
  href: '',
};
const defaultValueProp: WidgetValueProp = {
  src: '',
  alt: '',
  ...defaultValuePropActionLink,
};

export const defaultValueStateActionLink = {
  urlOrMailtoOrTel: '',
  subject: '',
  bodyOrMessage: '', // Send Email -> Body | Send SMS -> Message
  type: WidgetActionType.open_web_page,
};
const defaultValueState: WidgetValueState = {
  src: '',
  alt: '',
  ...defaultValueStateActionLink,
};

export function transformPropToStateActionLink(valueProp: WidgetValuePropActionLink): WidgetValueStateActionLink {
  const href = valueProp.href || '';
  const urlObj = (() => {
    try {
      return new URL(href);
    } catch (err) {
      return {
        href: '',
        searchParams: {
          get: (key: string) => '',
        },
      };
    }
  })();

  let urlOrMailtoOrTel = '';
  let subject = '';
  let bodyOrMessage = '';
  let type: WidgetActionType = WidgetActionType.open_web_page;

  urlOrMailtoOrTel = urlObj.href || href;

  if (href.startsWith('mailto:')) {
    /** "mailto:test@gmail.com?subject=ahihi&body=body" */
    type = WidgetActionType.send_email;
    subject = urlObj.searchParams.get('subject') || '';
    bodyOrMessage = urlObj.searchParams.get('body') || '';
  } else if (href.startsWith('tel:')) {
    /** "tel:0343261825" */
    type = WidgetActionType.make_call;
  } else if (href.startsWith('sms:')) {
    /** sms:0343261825?message=message_ne */
    type = WidgetActionType.send_sms;
    bodyOrMessage = urlObj.searchParams.get('message') || '';
  }

  return { urlOrMailtoOrTel, subject, bodyOrMessage, type };
}
function transformPropToState(valueProp: WidgetValueProp): WidgetValueState {
  try {
    const src = valueProp.src;
    const alt = valueProp.alt;
    const { urlOrMailtoOrTel, subject, bodyOrMessage, type } = transformPropToStateActionLink({
      href: valueProp.href,
    });

    return { src, alt, urlOrMailtoOrTel, subject, bodyOrMessage, type };
  } catch (err) {
    console.log('[WidgetImageUrl]', err);

    return defaultValueState;
  }
}

export function transformStateToPropActionLink(valueState: WidgetValueStateActionLink): WidgetValuePropActionLink {
  let href = valueState.urlOrMailtoOrTel;

  if (valueState.type === WidgetActionType.send_email) {
    const mailTo = valueState.urlOrMailtoOrTel;
    const subject = valueState.subject;
    const body = valueState.bodyOrMessage;
    href = `mailto:${mailTo}?subject=${subject}&body=${body}`;
  } else if (valueState.type === WidgetActionType.make_call) {
    href = `tel:${valueState.urlOrMailtoOrTel}`;
  } else if (valueState.type === WidgetActionType.send_sms) {
    href = `sms:${valueState.urlOrMailtoOrTel}?message=${valueState.bodyOrMessage}`;
  }

  return { href };
}
function transformStateToProp(valueState: WidgetValueState): WidgetValueProp {
  const src = valueState.src;
  const alt = valueState.alt;
  const { href } = transformStateToPropActionLink({
    urlOrMailtoOrTel: valueState.urlOrMailtoOrTel,
    subject: valueState.subject,
    bodyOrMessage: valueState.bodyOrMessage,
    type: valueState.type,
  });

  return { src, alt, href };
}

type WidgetActionLinkProps = {
  label: string;
  value: WidgetValueStateActionLink;
  handleChange: (evt: any) => void;
  handleChangeType: (val: string) => void;
};
export function WidgetActionLink({ label, value, handleChange, handleChangeType }: WidgetActionLinkProps) {
  return (
    <WidgetWrap>
      <label>{label}</label>
      <Select
        showSearch={false} //
        value={value.type}
        onChange={handleChangeType}
      >
        <Select.Option value={WidgetActionType.open_web_page}>Open web page</Select.Option>
        <Select.Option value={WidgetActionType.send_email}>Send email</Select.Option>
        <Select.Option value={WidgetActionType.make_call}>Make call</Select.Option>
        <Select.Option value={WidgetActionType.send_sms}>Send SMS</Select.Option>
      </Select>
      <StyledInputWrap>
        {value.type === WidgetActionType.open_web_page && (
          <Input
            key={value.type}
            addonBefore="Url"
            max={256}
            value={value.urlOrMailtoOrTel}
            name="urlOrMailtoOrTel"
            onChange={handleChange}
          />
        )}
        {value.type === WidgetActionType.send_email && (
          <Fragment key={value.type}>
            <Input
              addonBefore="Mailto" //
              max={256}
              value={value.urlOrMailtoOrTel}
              name="urlOrMailtoOrTel"
              onChange={handleChange}
            />
            <Input addonBefore="Subject" max={256} value={value.subject} name="subject" onChange={handleChange} />
            <Input
              addonBefore="Body" //
              max={256}
              value={value.bodyOrMessage}
              name="bodyOrMessage"
              onChange={handleChange}
            />
          </Fragment>
        )}
        {value.type === WidgetActionType.make_call && (
          <Input
            key={value.type} //
            addonBefore="Tel"
            max={256}
            value={value.urlOrMailtoOrTel}
            name="urlOrMailtoOrTel"
            onChange={handleChange}
          />
        )}
        {value.type === WidgetActionType.send_sms && (
          <Fragment key={value.type}>
            <Input
              addonBefore="Tel" //
              max={256}
              value={value.urlOrMailtoOrTel}
              name="urlOrMailtoOrTel"
              onChange={handleChange}
            />
            <Input
              addonBefore="Message" //
              max={256}
              value={value.bodyOrMessage}
              name="bodyOrMessage"
              onChange={handleChange}
            />
          </Fragment>
        )}
      </StyledInputWrap>
    </WidgetWrap>
  );
}

function WidgetImageUrl({ value: valueProp, initValue, onChange }: WidgetProps<WidgetValueProp>) {
  const [value, setValue] = useState(() => {
    return transformPropToState(initValue || valueProp || defaultValueProp);
  });

  useEffect(() => {
    if (valueProp) {
      // setValue(transformPropToState(valueProp));
    }
  }, [valueProp]);

  const _onChange = (newValueState: WidgetValueState) => {
    setValue(newValueState);
    onChange(WidgetPath.ImageUrl, transformStateToProp(newValueState));
  };

  function handleChange(evt: any) {
    const newValueState = {
      ...value,
      [evt.target.name]: evt.target.value,
    };
    _onChange(newValueState);
  }

  function handleChangeType(val: string) {
    const newValueState: WidgetValueState = {
      ...value,
      urlOrMailtoOrTel: '',
      bodyOrMessage: '',
      subject: '',
      type: val as WidgetActionType,
    };
    _onChange(newValueState);
  }

  return (
    <div>
      <WidgetWrap>
        <label>Image</label>
        <StyledInputWrap>
          <Input addonBefore="Url" max={256} value={value.src} name="src" onChange={handleChange} />
        </StyledInputWrap>
      </WidgetWrap>
      <WidgetWrap>
        <StyledInputWrap>
          <Input addonBefore="Alternate text" max={256} value={value.alt} name="alt" onChange={handleChange} />
        </StyledInputWrap>
      </WidgetWrap>
      <WidgetSectionTitle title="Action" />
      {/* TODO */}
      <WidgetActionLink label="Image Link" value={value} handleChange={handleChange} handleChangeType={handleChangeType} />
    </div>
  );
}

export { WidgetImageUrl };
