import { Meta } from '@storybook/react';
import React from 'react';
import styled from 'styled-components';
import ColumnBlockComp from './index';
import * as IconsColSvg from '../icons/columns';
import * as IconsRowsSvg from '../icons/rowsTemplate';

export default {
  title: 'Atoms/ColumnBlock',
  argTypes: {},
} as Meta;

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Item = styled.div`
  width: 320px;
  margin-right: 16px;
  margin-bottom: 15px;
`;

export const ColumnBlock = () => {
  const keys = Object.keys(IconsColSvg) as (ColumnType | RowDefaultType)[];
  const keysRow = Object.keys(IconsRowsSvg) as (ColumnType | RowDefaultType)[];

  return (
    <Container>
      {keys.concat(keysRow).map((iconType) => {
        return (
          <Item key={iconType}>
            <ColumnBlockComp icon={iconType} />
          </Item>
        );
      })}
    </Container>
  );
};
