import React, { useEffect, useState } from 'react';
import { Modal } from 'antd';
import { WidgetProps, WidgetPath } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';
import { SketchPicker } from 'react-color';

export const StyledColorWrap = styled.div`
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  min-width: 110px;
  display: flex;
  align-items: center;
  height: 32px;
  background-color: #fff;
  padding: 0 5px;
  cursor: pointer;

  .box-color {
    width: 20px;
    min-width: 20px;
    height: 20px;
    border-radius: 2px;
    border: 1px solid rgba(200, 200, 200, 0.45);

    &[style*='background-color: transparent'] {
      background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAAXNSR0IArs4c6QAAADFJREFUOE9jZGBgEGHAD97gk2YcNYBhmIQBgWSAP52AwoAQwJvQRg1gACckQoC2gQgAIF8IscwEtKYAAAAASUVORK5CYII=)
        left center;
    }
  }

  .value-color {
    font-size: 12px;
    padding-left: 5px;
  }
`;

export const StyledModal = styled(Modal)`
  .ant-modal-close {
    display: none;
  }
  .ant-modal-body {
    padding: 16px;
  }
`;

export const styles: any = {
  picker: {
    boxSizing: 'border-box',
    width: '100%',
    padding: '0',
  },
};

type PropsColor = {
  originColor: string;
  color: string;
  setColor: React.Dispatch<React.SetStateAction<string>>;
  isOpen: boolean;
  setIsOpen: React.Dispatch<React.SetStateAction<boolean>>;
  path: string;
  name?: string;
  onChange: (path: string, value: string, name?: string) => void;
};

const presetColors = [
  '#555555', //
  '#BBBBBB',
  '#2d87f3',
  '#335BA2',
  { color: 'transparent', title: 'Clear' },
];

export function ColorComponent({
  originColor,
  color, //
  setColor,
  isOpen,
  setIsOpen,
  path,
  onChange,
  name,
}: PropsColor) {
  return (
    <StyledModal
      visible={isOpen}
      closeIcon={<span />}
      onOk={() => {
        onChange(path, color, name);
        setIsOpen(false);
      }}
      onCancel={() => {
        setColor(originColor);
        setIsOpen(false);
      }}
      okText="Save"
      cancelText="Cancel"
      width={300}
    >
      <SketchPicker
        color={color}
        width="100%"
        styles={styles}
        onChange={(sketch) => {
          const hex = sketch.hex;
          const { r, g, b, a } = sketch.rgb;
          if (a && Number(a) < 1) {
            const newColor = `rgba(${r}, ${g}, ${b}, ${a})`;
            setColor(newColor);
          } else {
            setColor(hex);
          }
        }}
        presetColors={presetColors}
      />
    </StyledModal>
  );
}

type Props = WidgetProps<string> & {
  label: string;
  path: string;
};

export function WidgetColor({ label, path, value, initValue, onChange, name }: Props) {
  const [isOpen, setIsOpen] = useState(false);
  const [color, setColor] = useState(initValue || value || '');

  useEffect(() => {
    if (value) {
      setColor(value);
    }
  }, [value]);

  return (
    <WidgetWrap>
      <label>{label}</label>
      <StyledColorWrap onClick={() => setIsOpen(true)}>
        <div className="box-color" style={{ backgroundColor: color }}></div>
        <span className="value-color">{color}</span>
      </StyledColorWrap>
      <ColorComponent
        originColor={initValue || value || ''}
        color={color} //
        setColor={setColor}
        isOpen={isOpen}
        setIsOpen={setIsOpen}
        path={path}
        name={name}
        onChange={onChange}
      />
    </WidgetWrap>
  );
}

function WidgetTextColor(props: WidgetProps<string>) {
  return <WidgetColor {...props} label="Text color" path={WidgetPath.TextColor} />;
}

export { WidgetTextColor };
