import { Row, Switch, Input, Checkbox } from 'antd';
import React, { useEffect, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import { StyledBgImage } from './index.styled';

export type TypeBgImageValue = {
  'background-image': string;
  'background-position': string;
  'background-repeat': string;
  'background-size': string;
};

export const defaultWidgetBgImage = {
  'background-image': '',
  'background-position': 'top left', // "top left" | "center"
  'background-repeat': 'no-repeat',
  'background-size': 'auto', // Fit to background: cover | auto
};
export const defaultPropWidgetBgImage = {
  'background-image': 'none',
  'background-position': 'top left',
  'background-repeat': 'no-repeat',
  'background-size': 'auto',
};

const transformToValueBgImage = (propValue: TypeBgImageValue) => {
  try {
    const returnValue = { ...propValue };

    if (propValue['background-image'].startsWith('url(')) {
      returnValue['background-image'] = returnValue['background-image'].replace(`url('`, '').replace(`')`, '');
    } else if (propValue['background-image'] === 'none') {
      returnValue['background-image'] = '';
    }

    if (propValue['background-size'] === 'cover') {
      /** Fit to background */
      propValue['background-repeat'] = 'no-repeat';
      propValue['background-position'] = 'top left';
    } else {
      propValue['background-size'] = 'auto';
    }

    return {
      'background-image': returnValue['background-image'],
      'background-position': returnValue['background-position'],
      'background-size': returnValue['background-size'],
      'background-repeat': returnValue['background-repeat'],
    };
  } catch (err) {
    return defaultWidgetBgImage;
  }
};

const transformValueToProp = (value: TypeBgImageValue, isUseBg: boolean = true) => {
  const returnValue = { ...value };

  if (!value['background-image'] && !isUseBg) {
    returnValue['background-image'] = 'none';
  } else {
    returnValue['background-image'] = `url('${returnValue['background-image']}')`;
  }

  return {
    'background-image': returnValue['background-image'],
    'background-position': returnValue['background-position'],
    'background-size': returnValue['background-size'],
    'background-repeat': returnValue['background-repeat'],
  };
};

