type PropsWidgetBackgroundImage = WidgetProps<TypeBgImageValue> & {
  label: string;
  path: string;
};

export function WidgetBackgroundImage({ label, path, value: valueProp, initValue, onChange }: PropsWidgetBackgroundImage) {
  const [value, setValue] = useState<TypeBgImageValue>(() => {
    return initValue ? transformToValueBgImage(initValue) : defaultWidgetBgImage;
  });

  const [isUseBg, setIsUseBg] = useState(valueProp?.['background-image'] !== 'none');
  // const [isTouched, setIsTouched] = useState(!!value['background-image']);

  useEffect(() => {
    if (valueProp) {
      const newValue = transformToValueBgImage(valueProp);
      setValue(newValue);
      setIsUseBg(valueProp['background-image'] !== 'none');
    }
  }, [valueProp]);

  const handleChangeSwitch = (checked: boolean) => {
    setIsUseBg(checked);
    if (checked) {
      // setIsTouched(false);
    } else {
      setValue(defaultWidgetBgImage);
      onChange(path, transformValueToProp(defaultWidgetBgImage, checked));
    }
  };

  const handleChangeFit = (checked: boolean) => {
    const newValue = { ...value };
    if (checked) {
      newValue['background-size'] = 'cover';
      newValue['background-repeat'] = 'no-repeat';
      newValue['background-position'] = 'top left';
    } else {
      newValue['background-size'] = 'auto';
    }
    setValue(newValue);
    onChange(path, transformValueToProp(newValue));
  };

  const handleChangeUrl = (evt: any) => {
    const newValue = { ...value };
    newValue['background-image'] = evt.target.value;
    // setIsTouched(true);
    setValue(newValue);
    onChange(path, transformValueToProp(newValue));
  };

  const handleChangeRepeat = (evt: any) => {
    const newValue = { ...value };
    if (evt.target.checked) {
      newValue['background-repeat'] = 'repeat';
    } else {
      newValue['background-repeat'] = 'no-repeat';
    }
    setValue(newValue);
    onChange(path, transformValueToProp(newValue));
  };

  const handleChangeCenter = (evt: any) => {
    const newValue = { ...value };
    if (evt.target.checked) {
      newValue['background-position'] = 'top center';
    } else {
      newValue['background-position'] = 'top left';
    }
    setValue(newValue);
    onChange(path, transformValueToProp(newValue));
  };

  // const url = value['background-image'];
  // const status = isTouched && !checkUrlIsValid(url) ? 'error' : undefined;

  return (
    <WidgetWrap>
      <label>{label}</label>
      <Switch size="small" checked={isUseBg} onChange={handleChangeSwitch} />
      {isUseBg && (
        <StyledBgImage>
          <Input addonBefore="Url" value={value['background-image']} onChange={handleChangeUrl} />

          <Row justify="space-between" align="middle" className="bg-properties">
            <Row align="middle">
              <Switch size="small" onChange={handleChangeFit} checked={value['background-size'] === 'cover'} />
              &nbsp;&nbsp;<span>Fit to background</span>
            </Row>

            <Row align="middle">
              <Checkbox
                onChange={handleChangeRepeat}
                checked={value['background-repeat'] === 'repeat'}
                disabled={value['background-size'] === 'cover'}
              >
                Repeat
              </Checkbox>
              <Checkbox
                onChange={handleChangeCenter}
                checked={value['background-position'] === 'top center'}
                disabled={value['background-size'] === 'cover'}
              >
                Center
              </Checkbox>
            </Row>
          </Row>
        </StyledBgImage>
      )}
    </WidgetWrap>
  );
}

function WidgetBodyBgImage(props: WidgetProps<TypeBgImageValue>) {
  return <WidgetBackgroundImage {...props} label="Background image" path={WidgetPath.BodyBgImage} />;
}

export { WidgetBodyBgImage };
