import React from 'react';
import styled from 'styled-components';
import Icon from '../icons/Icon';
const Panel = styled.div`
  padding: 15px;
  border: #dddddd 1px solid;
  box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
  transition: all 0.15s;

  svg {
    display: block;
  }

  span {
    color: #8f9699;
    display: block;
    font-size: 12px;
    text-align: center;
    line-height: 1;
    padding-top: 5px;
  }

  &:hover {
    cursor: grab;
    border: 1px solid #ffffff;
    box-shadow: 0 6px 10px rgb(0 0 0 / 35%);
  }
`;

interface Props extends React.HTMLProps<HTMLDivElement> {
  icon: ColumnType | RowDefaultType;
}

const mapIconToLabel: Record<Partial<RowDefaultType>, string> = {
  'HHGFooter': 'HHG Footer',
};

function ColumnBlock({ icon, ...restProps }: Props) {
  const label = mapIconToLabel[icon as RowDefaultType];

  return (
    // @ts-ignore
    <Panel {...restProps}>
      <Icon name={icon} width="100%" height="100%" />
      {label && <span>{label}</span>}
    </Panel>
  );
}

export default ColumnBlock;
