import Icon from '../../icons/Icon';
import React, { useEffect, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';

export const StyledIcons = styled.div`
  display: flex;
  gap: 12px;

  > svg {
    opacity: 0.5;
    cursor: pointer;

    path {
      pointer-events: none;
    }

    &.active {
      opacity: 1;
    }
  }
`;

function WidgetListType({ value: valueProp = 'ul', initValue, onChange }: WidgetProps<string>) {
  const [value, setValue] = useState(initValue);

  useEffect(() => {
    if (valueProp) {
      setValue(valueProp);
    }
  }, [valueProp]);

  function handleClick(evt: any) {
    const dataType: string = evt.target.getAttribute('data-type');

    setValue(dataType);
    onChange(WidgetPath.ListType, dataType);
  }

  return (
    <WidgetWrap>
      <label>Line height</label>
      <StyledIcons>
        <Icon
          onClick={handleClick} //
          name="OrderedList"
          fill="#898989"
          className={value === 'ol' ? 'active' : ''}
          data-type="ol"
        />
        <Icon
          onClick={handleClick} //
          name="UnorderedList"
          fill="#898989"
          className={value === 'ul' ? 'active' : ''}
          data-type="ul"
        />
      </StyledIcons>
    </WidgetWrap>
  );
}

export { WidgetListType };
