import React from 'react';
import { convertStyleToJsx } from '../../utils';
import styled from 'styled-components';

export type PropsForm = {
  moduleBlockStyle: Record<string, string>;
  computedStyle: Record<string, string>; // hideContentOnMobile
  formStructure: Record<string, any>;
  formStyle: Record<string, any>;
};

const StyledRow = styled.div`
  display: flex;
  flex-flow: row wrap;
  flex-grow: 1;

  .form_field {
    padding: 3px;
    flex: 1 1 0%;
    display: block;
    flex-wrap: wrap;

    &.top {
      min-width: 0px;
      flex-basis: 100%;
      display: block;

      label {
        display: block;
      }
      input {
        width: 100%;
      }
    }

    &.side {
      display: flex;
      flex-wrap: nowrap;
      label {
        display: inline-block;
        min-width: auto !important;
      }
      input {
        width: auto;
        flex: 1;
      }
    }
  }
`;

const StyledForm = styled.div`
  margin: 0 auto;
`;

var rxCommonMarkLink = /(\[([^\]]+)])\(([^)]+)\)/g;

export function commonMarkLinkToAnchorTag(md: string) {
  const anchor = md ? md.replace(rxCommonMarkLink, '<a href="$3">$2</a>') : md;

  return anchor;
}

export function Form({ moduleBlockStyle, formStructure, formStyle: _formStyle }: PropsForm) {
  const { width: formWidth, ...formStyle } = _formStyle;
  const moduleStyleJsx = convertStyleToJsx({ ...moduleBlockStyle, width: formWidth });
  const listFields = formStructure.fields;

  const renderFormField = (fieldData: any) => {
    const { 'label-position': labelPosition, ...restLabelsStyle } = formStyle.labels;
    const labelStyleJsx = convertStyleToJsx(restLabelsStyle);
    const fieldStyleJsx =
      fieldData.type === 'submit' //
        ? convertStyleToJsx(formStyle.buttons)
        : convertStyleToJsx(formStyle.fields);

    if (fieldData.type === 'checkbox') {
      return (
        <div className="form_field checkbox">
          <input id={fieldData.attributes.name} type={fieldData.type} {...fieldData.attributes} style={fieldStyleJsx} />
          <label
            htmlFor={fieldData.attributes.name}
            style={labelStyleJsx}
            dangerouslySetInnerHTML={{ __html: '&nbsp;' + commonMarkLinkToAnchorTag(fieldData.label) }}
          />
        </div>
      );
    }

    return (
      <div className={`form_field ${labelPosition}`}>
        {fieldData.label && <label style={labelStyleJsx}>{fieldData.label}</label>}
        <input id={fieldData.attributes.name} type={fieldData.type} {...fieldData.attributes} style={fieldStyleJsx} />
      </div>
    );
  };

  return (
    <StyledForm style={moduleStyleJsx}>
      {listFields.map((fieldData: any) => {
        return (
          <StyledRow key={fieldData.attributes.name} className="form_row">
            {renderFormField(fieldData)}
          </StyledRow>
        );
      })}
    </StyledForm>
  );
}
