import styled from 'styled-components';

export const StyledParagraph = styled.div<{ $computedStyle: Record<string, string> }>`
  .ProseMirror {
    p {
      margin: 0;
    }
    ${(props) => {
      if (props.$computedStyle.paragraphSpacing) {
        return `p + p { margin-top: ${props.$computedStyle.paragraphSpacing} }`;
      }

      return '';
    }}
  }
`;
