import React from 'react';
import Icon from '../icons/Icon';
import { StyledWrap, StyledAction } from './index.styled';

export type SidebarPanelTitleProps = {
  title: string;
  onAction: (type: 'delete' | 'duplicate' | 'hide') => void;
};

function SidebarPanelTitle({ title, onAction }: SidebarPanelTitleProps) {
  return (
    <StyledWrap className="sidebar-panel-title">
      <h4>{title}</h4>
      <StyledAction className="delete-icon" onClick={() => onAction('delete')}>
        <Icon name="Trash" width="20" height="20" />
      </StyledAction>
      <StyledAction className="duplicate-icon" onClick={() => onAction('duplicate')}>
        <Icon name="Duplicate" width="20" height="20" />
      </StyledAction>
      <StyledAction onClick={() => onAction('hide')}>
        <Icon name="ArrowDown" width="20" height="20" />
      </StyledAction>
    </StyledWrap>
  );
}

export { SidebarPanelTitle };
