import Icon from '../../icons/Icon';
import React, { useEffect, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';

export const StyledIcons = styled.div`
  display: flex;
  gap: 12px;

  > svg {
    opacity: 0.5;
    cursor: pointer;

    path {
      pointer-events: none;
    }

    &.active {
      opacity: 1;
    }
  }
`;

function WidgetLineHeight({ value: valueProp = '120%', initValue, onChange, name }: WidgetProps<string>) {
  const [value, setValue] = useState(initValue);

  useEffect(() => {
    if (valueProp) {
      setValue(valueProp);
    }
  }, [valueProp]);

  function handleClick(evt: any) {
    const dataLineHeight: string = evt.target.getAttribute('data-line-height');

    setValue(dataLineHeight);
    onChange(WidgetPath.LineHeight, dataLineHeight, name);
  }

  return (
    <WidgetWrap>
      <label>Line height</label>
      <StyledIcons>
        <Icon
          onClick={handleClick} //
          name="LineHeight120"
          fill="#898989"
          className={value === '120%' ? 'active' : ''}
          data-line-height="120%"
        />
        <Icon
          onClick={handleClick} //
          name="LineHeight150"
          fill="#898989"
          className={value === '150%' ? 'active' : ''}
          data-line-height="150%"
        />
        <Icon
          onClick={handleClick} //
          name="LineHeight180"
          fill="#898989"
          className={value === '180%' ? 'active' : ''}
          data-line-height="180%"
        />
        <Icon
          onClick={handleClick} //
          name="LineHeight200"
          fill="#898989"
          className={value === '200%' ? 'active' : ''}
          data-line-height="200%"
        />
      </StyledIcons>
    </WidgetWrap>
  );
}

export { WidgetLineHeight };
