import { Row, Switch, InputNumber, Col } from 'antd';
import Icon from '../../../atoms/icons/Icon';
import React, { useEffect, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { StyledWidgetWrap, StyledWidgetPadding, StyledPaddingPreview } from './index.styled';

export type TypePaddingValue = {
  'padding-top': string;
  'padding-right': string;
  'padding-bottom': string;
  'padding-left': string;
};

type WidgetPaddingValue = WidgetProps<TypePaddingValue> & {
  label: string;
  path: string;
};

function isEqualPadding(value?: TypePaddingValue, comparedValue?: TypePaddingValue) {
  if (!value || !comparedValue) {
    return false;
  }

  return (
    value['padding-top'] === comparedValue['padding-top'] &&
    value['padding-right'] === comparedValue['padding-right'] &&
    value['padding-bottom'] === comparedValue['padding-bottom'] &&
    value['padding-left'] === comparedValue['padding-left']
  );
}

function getIsShowMoreOpt(value?: TypePaddingValue) {
  if (!value) {
    return false;
  }

  const top = value['padding-top'];
  const right = value['padding-right'];
  const bottom = value['padding-bottom'];
  const left = value['padding-left'];

  return !(top === right && top === bottom && top === left);
}

export function WidgetStylePadding({ label, path, value: valueProp, initValue, onChange, name }: WidgetPaddingValue) {
  const [value, setValue] = useState<TypePaddingValue>(() => {
    return (
      initValue ||
      valueProp || {
        'padding-top': '0px',
        'padding-right': '0px',
        'padding-bottom': '0px',
        'padding-left': '0px',
      }
    );
  });
  const [isShowMoreOpts, setIsShowMoreOpts] = useState(() => {
    return getIsShowMoreOpt(initValue || valueProp);
  });

  useEffect(() => {
    if (valueProp) {
      setValue((v) => {
        if (isEqualPadding(v, valueProp)) {
          return v;
        }

        setIsShowMoreOpts(getIsShowMoreOpt(valueProp));

        return valueProp;
      });
    }
  }, [valueProp]);

  const handleChangeChecked = (newChecked: boolean) => {
    setIsShowMoreOpts(newChecked);
    if (!newChecked) {
      const newValue = {
        'padding-top': value['padding-top'],
        'padding-right': value['padding-top'],
        'padding-bottom': value['padding-top'],
        'padding-left': value['padding-top'],
      };
      setValue(newValue);
      onChange(path, newValue, name);
    }
  };

  const onChangeAllSide = (num: number) => {
    if (num === null) {
      return;
    }

    const newValue = {
      'padding-top': `${num}px`,
      'padding-right': `${num}px`,
      'padding-bottom': `${num}px`,
      'padding-left': `${num}px`,
    };
    setValue(newValue);
    onChange(path, newValue, name);
  };

  const onChangeOneSide = (key: keyof TypePaddingValue, num: number) => {
    if (num === null) {
      return;
    }

    const newValue = {
      'padding-top': value['padding-top'],
      'padding-right': value['padding-right'],
      'padding-bottom': value['padding-bottom'],
      'padding-left': value['padding-left'],
      [key]: `${num}px`,
    };
    setValue(newValue);
    onChange(path, newValue, name);
  };

  return (
    <StyledWidgetWrap>
      <label>{label}</label>
      <Row align="middle">
        <span className="more-opts">More Options</span>&nbsp;&nbsp;
        <Switch size="small" checked={isShowMoreOpts} onChange={handleChangeChecked} />
      </Row>
      <StyledWidgetPadding>
        <Row justify="space-between" align="top">
          {!isShowMoreOpts ? (
            <div>
              <p>All sides</p>
              <InputNumber
                min={0} //
                max={60}
                value={parseInt(value['padding-top'])}
                onChange={onChangeAllSide}
              />
            </div>
          ) : (
            <Row style={{ flex: 1 }}>
              <Col span={10}>
                <p>Top</p>
                <InputNumber
                  min={0} //
                  max={60}
                  value={parseInt(value['padding-top'])}
                  onChange={(num) => onChangeOneSide('padding-top', num)}
                />
                <p>Bottom</p>
                <InputNumber
                  min={0} //
                  max={60}
                  value={parseInt(value['padding-bottom'])}
                  onChange={(num) => onChangeOneSide('padding-bottom', num)}
                />
              </Col>
              <Col span={10}>
                <p>Left</p>
                <InputNumber
                  min={0} //
                  max={60}
                  value={parseInt(value['padding-left'])}
                  onChange={(num) => onChangeOneSide('padding-left', num)}
                />
                <p>Right</p>
                <InputNumber
                  min={0} //
                  value={parseInt(value['padding-right'])}
                  onChange={(num) => onChangeOneSide('padding-right', num)}
                />
              </Col>
            </Row>
          )}
          <StyledPaddingPreview $value={value}>
            <Icon name="Content" />
          </StyledPaddingPreview>
        </Row>
      </StyledWidgetPadding>
    </StyledWidgetWrap>
  );
}

function WidgetPadding(props: WidgetProps<TypePaddingValue>) {
  return <WidgetStylePadding {...props} label="Padding" path={WidgetPath.Padding} />;
}

export { WidgetPadding };
