import React, { useEffect, useState } from 'react';
import { Button } from 'antd';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';

export const StyledButtons = styled.div`
  display: flex;

  > button {
    min-width: 76px;

    &[data-align='left'] {
      border-radius: 4px 0 0 4px;
    }
    &[data-align='center'] {
      border-radius: 0 4px 4px 0;
    }

    > span {
      pointer-events: none;
    }
  }
`;

function WidgetContentAreaAlign({ value: valueProp, initValue, onChange }: WidgetProps<string>) {
  const [value, setValue] = useState(initValue || 'center');

  useEffect(() => {
    if (valueProp) {
      setValue(valueProp);
    }
  }, [valueProp]);

  function handleClick(evt: any) {
    const dataAlign: string = evt.target.getAttribute('data-align');
    setValue(dataAlign);
    onChange(WidgetPath.ContentAreaAlign, dataAlign);
  }

  return (
    <WidgetWrap>
      <label>Content area alignment</label>
      <StyledButtons>
        <Button data-align="left" onClick={handleClick} type={value === 'left' ? 'primary' : undefined}>
          Left
        </Button>
        <Button data-align="center" onClick={handleClick} type={value === 'center' ? 'primary' : undefined}>
          Center
        </Button>
      </StyledButtons>
    </WidgetWrap>
  );
}

export { WidgetContentAreaAlign };
