import React, { useEffect, useRef, useState } from 'react';
import { WidgetPath, WidgetProps } from '../index';
import { Controlled as CodeMirror } from 'react-codemirror2';
import styled from 'styled-components';
import 'codemirror/mode/xml/xml.js';
import { Spin } from 'antd';

const StyledWrap = styled.div`
  margin-top: 15px;
  border-top: 1px solid #e1e4e7;
  border-bottom: 1px solid #e1e4e7;

  .react-codemirror2 {
    font-size: 12px;
  }

  > h3,
  > p {
    color: #8f9699;
    padding: 0 30px;
  }

  > p {
    font-size: 12px;
  }
  > h3 {
    padding-top: 15px;
    font-weight: 600;
  }

  .wrap-spiner {
    text-align: center;
    padding-top: 10px;
  }
`;

function WidgetHtmlEditor({ value: valueProp, initValue: initValueProp, onChange, name }: WidgetProps<string>) {
  const editorRef = useRef<any>();
  const [delayShow, setDelayShow] = useState(false);
  const [value, setValue] = useState(initValueProp || valueProp || '');

  useEffect(() => {
    setTimeout(() => {
      setDelayShow(true);
    }, 100);
  }, []);

  useEffect(() => {
    delayShow && editorRef.current?.focus();
  }, [delayShow]);

  return (
    <StyledWrap>
      {delayShow ? (
        <CodeMirror
          value={value}
          editorDidMount={(editor) => (editorRef.current = editor)}
          options={{
            mode: 'xml',
            theme: 'default',
            lineNumbers: true,
          }}
          onBeforeChange={(editor, data, value) => {
            setValue(value);
          }}
          onBlur={() => {
            onChange(WidgetPath.HtmlEditor, value, name);
          }}
        />
      ) : (
        <div className="wrap-spiner">
          <Spin />
        </div>
      )}
      <h3>HTML experts only</h3>
      <p>Using your own code may affect how the message is rendered. Make sure to use correct and responsive HTML.</p>
    </StyledWrap>
  );
}

export { WidgetHtmlEditor };
