import React from 'react';
import { WidgetPath, WidgetProps } from '..';
import { Collapse } from 'antd';
import { WidgetColor } from '../widgetTextColor';
import { WidgetModulePadding } from '../widgetModulePadding';
import styled from 'styled-components';
import { getBorderValue, getPaddingValue } from '../widgetFormButtonOpts';
import { WidgetBackgroundColor } from '../widgetBackgroundColor';
import { WidgetBorderRadius } from '../widgetBorderRadius';
import { WidgetModuleBorder } from '../widgetModuleBorder';

type WidgetValueProp = Record<string, string>;

const { Panel } = Collapse;

const StyledCollapse = styled(Collapse)`
  font-size: 12px;
  margin: 15px 20px;
  background-color: #eaeaea;

  & > .ant-collapse-item > .ant-collapse-header {
    padding: 12px 16px 10px 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #505659;
  }
  .ant-collapse-content > .ant-collapse-content-box {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    padding-bottom: 0px;

    > div:last-child {
      border-bottom: 0;
    }
  }
`;

function WidgetFormFieldOpts({ value, initValue, onChange, name: widgetName }: WidgetProps<WidgetValueProp>) {
  const handleChange = (_: string, singleVal: string | Record<string, string>, name?: string) => {
    if (!name || !value) {
      return;
    }

    if (typeof singleVal === 'string') {
      onChange(WidgetPath.FormFieldOpts, { ...value, [name]: singleVal }, widgetName);
    } else if (typeof singleVal === 'object') {
      onChange(WidgetPath.FormFieldOpts, { ...value, ...singleVal }, widgetName);
    }
  };

  return (
    <StyledCollapse>
      <Panel header="Field Options" key="fieldOptions">
        <WidgetColor
          label="Text color" //
          path=""
          value={value?.['color'] || ''}
          name="color"
          onChange={handleChange}
        />
        <WidgetBackgroundColor
          value={value?.['background-color'] || ''} //
          name="background-color"
          onChange={handleChange}
        />
        <WidgetModulePadding
          value={getPaddingValue(value)} //
          name="padding"
          onChange={handleChange}
        />
        <WidgetModuleBorder
          value={getBorderValue(value)} //
          name="border"
          onChange={handleChange}
        />
        <WidgetBorderRadius
          value={value?.['border-radius'] || '0px'} //
          name="border-radius"
          onChange={handleChange}
        />
        <WidgetColor
          label="Active field outline color" //
          path=""
          value={value?.['outline-color'] || ''}
          name="outline-color"
          onChange={handleChange}
        />
      </Panel>
    </StyledCollapse>
  );
}

export { WidgetFormFieldOpts };
