import React from 'react';
import { WidgetPath, WidgetProps } from '..';
import { Collapse } from 'antd';
import { WidgetTextColor } from '../widgetTextColor';
import { WidgetTextAlign } from '../widgetTextAlign';
import { WidgetLetterSpacing } from '../widgetLetterSpacing';
import { WidgetModulePadding } from '../widgetModulePadding';
import { WidgetFontWeight } from '../widgetFontWeight';
import styled from 'styled-components';
import { WidgetLineHeight } from '../widgetLineHeight';
import { getPaddingValue } from '../widgetFormButtonOpts';
import { WidgetFormLabelPosition } from '../widgetFormLabelPosition';

type WidgetValueProp = Record<string, string>;

const { Panel } = Collapse;

const StyledCollapse = styled(Collapse)`
  font-size: 12px;
  margin: 15px 20px;
  background-color: #eaeaea;

  & > .ant-collapse-item > .ant-collapse-header {
    padding: 12px 16px 10px 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #505659;
  }
  .ant-collapse-content > .ant-collapse-content-box {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    padding-bottom: 0px;

    > div:last-child {
      border-bottom: 0;
    }
  }
`;

function WidgetFormLabelOpts({ value, initValue, onChange, name: widgetName }: WidgetProps<WidgetValueProp>) {
  const handleChange = (_: string, singleVal: string | Record<string, string>, name?: string) => {
    if (!name || !value) {
      return;
    }

    if (typeof singleVal === 'string') {
      onChange(WidgetPath.FormLabelOpts, { ...value, [name]: singleVal }, widgetName);
    } else if (typeof singleVal === 'object') {
      onChange(WidgetPath.FormLabelOpts, { ...value, ...singleVal }, widgetName);
    }
  };

  return (
    <StyledCollapse>
      <Panel header="Label Options" key="labelOptions">
        <WidgetTextColor value={value?.['color'] || ''} name="color" onChange={handleChange} />
        <WidgetTextAlign
          value={value?.['text-align'] || 'left'} //
          name="text-align"
          onChange={handleChange}
        />
        <WidgetLineHeight
          value={value?.['line-height'] || '120%'} //
          name="line-height"
          onChange={handleChange}
        />
        <WidgetLetterSpacing
          value={value?.['letter-spacing'] || '0px'} //
          name="letter-spacing"
          onChange={handleChange}
        />
        <WidgetModulePadding
          value={getPaddingValue(value)} //
          name="padding"
          onChange={handleChange}
        />
        <WidgetFontWeight
          value={value?.['font-weight'] || '400'} //
          name="font-weight"
          onChange={handleChange}
        />
        <WidgetFormLabelPosition
          value={value?.['label-position'] || 'top'} //
          name="label-position"
          onChange={handleChange}
        />
      </Panel>
    </StyledCollapse>
  );
}

export { WidgetFormLabelOpts };
