import React from 'react';
import styled from 'styled-components';
import { ThemeProps } from '../../providers/theme';
import Icon from '../icons/Icon';

const Panel = styled.div`
  border-radius: 4px;
  background-color: #fff;
  min-height: 115px;
  padding: 18px 15px 15px 15px;
  border: ${(props: ThemeProps) => props.theme.colors.gray200} 1px solid;
  box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
  color: ${(props: ThemeProps) => props.theme.colors.gray600};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition-duration: 0.15s;
  transition-property: box-shadow, -webkit-box-shadow;
  cursor: pointer;

  &:hover {
    border: 1px solid #ffffff;
    box-shadow: 0 6px 10px rgb(0 0 0 / 35%);
  }

  &[disabled] {
    opacity: 0.6;
    cursor: not-allowed;
    &:hover {
      box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
      border: ${(props: ThemeProps) => props.theme.colors.gray200} 1px solid;
    }
  }
`;

const PanelTitle = styled.p`
  margin-top: 10px;
  margin-bottom: 0;
  font-size: 12px;
  text-transform: uppercase;
  word-break: break-word;
  word-wrap: break-word;
`;

interface Props extends React.HTMLProps<HTMLDivElement> {
  icon: IconType;
}

const mapIconToLabel: Record<string, string> = {
  'Footer': 'HHG Footer',
  'HHGSocial': 'HHG Social',
};

function IconBlock({ icon, children, ...restProps }: Props) {
  return (
    // @ts-ignore
    <Panel {...restProps}>
      <Icon name={icon} />
      <PanelTitle>{mapIconToLabel[icon] || icon}</PanelTitle>
    </Panel>
  );
}

export default IconBlock;
