import React from 'react';
import { WidgetPath, WidgetProps } from '..';
import { Collapse } from 'antd';
import { WidgetTextColor } from '../widgetTextColor';
import { WidgetBackgroundColor } from '../widgetBackgroundColor';
import { WidgetTextAlign } from '../widgetTextAlign';
import { WidgetLetterSpacing } from '../widgetLetterSpacing';
import { WidgetModulePadding } from '../widgetModulePadding';
import { WidgetBorderRadius } from '../widgetBorderRadius';
import { WidgetFontWeight } from '../widgetFontWeight';
import { WidgetModuleBorder } from '../widgetModuleBorder';
import styled from 'styled-components';

type WidgetValueProp = Record<string, string>;

const { Panel } = Collapse;

export function getPaddingValue(value?: Record<string, string>) {
  return {
    'padding-top': value?.['padding-top'] || '0px',
    'padding-right': value?.['padding-right'] || '0px',
    'padding-bottom': value?.['padding-bottom'] || '0px',
    'padding-left': value?.['padding-left'] || '0px',
  };
}

export function getBorderValue(value?: Record<string, string>) {
  return {
    'border-top': value?.['border-top'] || '0px solid transparent',
    'border-right': value?.['border-right'] || '0px solid transparent',
    'border-bottom': value?.['border-bottom'] || '0px solid transparent',
    'border-left': value?.['border-left'] || '0px solid transparent',
  };
}

const StyledCollapse = styled(Collapse)`
  font-size: 12px;
  margin: 15px 20px;
  background-color: #eaeaea;

  & > .ant-collapse-item > .ant-collapse-header {
    padding: 12px 16px 10px 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #505659;
  }
  .ant-collapse-content > .ant-collapse-content-box {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    padding-bottom: 0px;

    > div:last-child {
      border-bottom: 0;
    }
  }
`;

function WidgetFormButtonOpts({ value, initValue, onChange, name: widgetName }: WidgetProps<WidgetValueProp>) {
  const handleChange = (_: string, singleVal: string | Record<string, string>, name?: string) => {
    if (!name || !value) {
      return;
    }

    if (typeof singleVal === 'string') {
      onChange(WidgetPath.FormButtonOpts, { ...value, [name]: singleVal }, widgetName);
    } else if (typeof singleVal === 'object') {
      onChange(WidgetPath.FormButtonOpts, { ...value, ...singleVal }, widgetName);
    }
  };

  return (
    <StyledCollapse>
      <Panel header="Button Options" key="btnOptions">
        <WidgetTextColor value={value?.['color'] || ''} name="color" onChange={handleChange} />
        <WidgetBackgroundColor
          value={value?.['background-color'] || ''} //
          name="background-color"
          onChange={handleChange}
        />
        <WidgetTextAlign
          value={value?.['text-align'] || 'left'} //
          name="text-align"
          onChange={handleChange}
        />
        <WidgetLetterSpacing
          value={value?.['letter-spacing'] || '0px'} //
          name="letter-spacing"
          onChange={handleChange}
        />
        <WidgetModulePadding
          value={getPaddingValue(value)} //
          name="padding"
          onChange={handleChange}
        />
        <WidgetModuleBorder
          value={getBorderValue(value)} //
          name="border"
          onChange={handleChange}
        />
        <WidgetFontWeight
          value={value?.['font-weight'] || '400'} //
          name="font-weight"
          onChange={handleChange}
        />
        <WidgetBorderRadius
          value={value?.['border-radius'] || '0px'} //
          name="border-radius"
          onChange={handleChange}
        />
      </Panel>
    </StyledCollapse>
  );
}

export { WidgetFormButtonOpts };
