import React, { useEffect, useState } from 'react';
import { Button } from 'antd';
import { WidgetPath, WidgetProps } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';

export const StyledButtons = styled.div`
  display: flex;

  > button {
    min-width: 76px;

    &[data-align='left'] {
      border-radius: 4px 0 0 4px;
    }
    &[data-align='center'] {
      border-radius: 0 4px 4px 0;
    }

    > span {
      pointer-events: none;
    }
  }
`;

function WidgetFormLabelPosition({ value: valueProp, initValue, onChange, name }: WidgetProps<string>) {
  const [value, setValue] = useState(initValue || 'top');

  useEffect(() => {
    if (valueProp) {
      setValue(valueProp);
    }
  }, [valueProp]);

  function handleClick(evt: any) {
    const dataAlign: string = evt.target.getAttribute('data-align');
    setValue(dataAlign);
    onChange(WidgetPath.FormLabelPosition, dataAlign, name);
  }

  return (
    <WidgetWrap>
      <label>Content area alignment</label>
      <StyledButtons>
        <Button data-align="top" onClick={handleClick} type={value === 'top' ? 'primary' : undefined}>
          Top
        </Button>
        <Button data-align="side" onClick={handleClick} type={value === 'side' ? 'primary' : undefined}>
          Side
        </Button>
      </StyledButtons>
    </WidgetWrap>
  );
}

export { WidgetFormLabelPosition };
