import React, { useRef, useState } from 'react';
import { WidgetPath, WidgetProps } from '..';
import { Collapse, Button, Input, Modal, Form, Select, Switch } from 'antd';
import { MenuOutlined, SettingOutlined, CheckSquareOutlined } from '@ant-design/icons';
import styled from 'styled-components';
import { WidgetSectionTitle } from '../../../atoms/widgetSectionTitle';
import Icon from '../../../atoms/icons/Icon';
import {
  DragDropContext, //
  Droppable,
  Draggable,
  DropResult,
} from 'react-beautiful-dnd';

export type WidgetValueProp = {
  fields: TypeFormField[];
};

const { Panel } = Collapse;

const StyledCollapse = styled(Collapse)`
  font-size: 12px;
  margin: 15px 20px;
  background-color: #eaeaea;

  & > .ant-collapse-item > .ant-collapse-header {
    padding: 12px 16px 10px 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #505659;
  }
  .ant-collapse-content > .ant-collapse-content-box {
    background-color: #f9f9f9;
    padding: 0 10px 10px 10px;
  }

  .btn-add-field {
    margin-top: 12px;
  }
`;

const StyledField = styled.div`
  background-color: #ffffff;
  border-color: #dddddd;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px 12px 8px;
  margin-top: 12px;
  display: flex;

  .field-draggable {
    width: 30px;
    padding-top: 2px;
    padding-left: 2px;

    .icon-to-drag {
      font-size: 18px;
      display: inline-flex;
      cursor: pointer;
    }
  }

  .field-area {
    flex: 1;
  }

  .field-area-bottom {
    padding-top: 5px;
    display: flex;
    justify-content: space-between;
  }

  .field-name {
    color: #8f9699;
    font-size: 12px;
  }

  .field-actions {
    span {
      font-size: 12px;
      font-weight: 500;
      cursor: pointer;
    }
  }

  .field-action-edit {
    color: #2d87f3;
  }

  .field-action-delete {
    color: #bb3636;
    margin-left: 8px;

    &:hover {
      text-decoration: underline;
    }

    &.disabled {
      color: #8f9699;
      cursor: not-allowed;

      &:hover {
        text-decoration: none;
      }
    }
  }
`;

const defaultFormData = {
  type: 'text',
  label: '',
  attrName: '',
  attrRequired: false,
  attrReadOnly: false,
};

const MapIconFields: Record<string, JSX.Element> = {
  text: <Icon name="FormText" />,
  email: <Icon name="FormText" />,
  checkbox: <CheckSquareOutlined />,
  submit: <Icon name="Button" width="1em" height="1em" />,
};

function WidgetFormManageFields({
  value, //
  initValue,
  onChange,
  name: widgetName,
}: WidgetProps<WidgetValueProp>) {
  const refDebounced = useRef<NodeJS.Timeout>();
  const [listFields, setListFields] = useState((value || initValue)?.fields || []);
  const [modalType, setModalType] = useState('');
  const [formData, setFormData] = useState(defaultFormData);

  function onFinishForm(values: any) {
    let newListFields = listFields;

    if (modalType === 'new') {
      const newFieldData: TypeFormField = {
        type: values.type,
        label: values.label,
        attributes: {
          name: values.attrName,
          required: values.attrRequired,
        },
      };

      newListFields = [...listFields];
      newListFields.splice(listFields.length - 1, 0, newFieldData);

      setListFields(newListFields);
      onChange(WidgetPath.FormManageFields, { fields: newListFields }, widgetName);
    } else if (modalType === 'edit') {
      const currName = formData.attrName;
      const newName = values.attrName;

      newListFields = listFields.map((f) => {
        if (f.attributes.name === currName) {
          return {
            ...f,
            type: values.type,
            label: values.label,
            attributes: {
              ...f.attributes,
              name: newName,
              required: values.attrRequired,
            },
          };
        }

        return f;
      });
      setListFields(newListFields);
      onChange(WidgetPath.FormManageFields, { fields: newListFields }, widgetName);
    }

    setModalType('');
  }

  function onClickEditField(field: TypeFormField) {
    return () => {
      setFormData({
        type: field.type,
        label: field.label,
        attrName: String(field.attributes.name),
        attrRequired: Boolean(field.attributes.required),
        attrReadOnly: false,
      });
      setModalType('edit');
    };
  }

  function onClickDeleteField(field: TypeFormField) {
    return () => {
      const newListFields = listFields.filter((f) => f.attributes.name !== field.attributes.name);
      setListFields(newListFields);
      onChange(WidgetPath.FormManageFields, { fields: newListFields }, widgetName);
    };
  }

  function onChangeLabel(field: TypeFormField) {
    return (evt: any) => {
      const newLabel = evt.target.value;
      const newListFields: TypeFormField[] = listFields.map((f) => {
        if (f.attributes.name === field.attributes.name) {
          if (f.type === 'submit') {
            return { ...f, attributes: { ...f.attributes, value: newLabel } };
          }

          return { ...f, label: newLabel };
        }

        return f;
      });
      setListFields(newListFields);

      if (refDebounced.current) {
        clearTimeout(refDebounced.current);
      }

      refDebounced.current = setTimeout(() => {
        refDebounced.current = undefined;
        onChange(WidgetPath.FormManageFields, { fields: newListFields }, widgetName);
      }, 300);
    };
  }

  function onDragEnd(result: DropResult) {
    if (
      result.reason === 'DROP' &&
      result.type === 'fields' &&
      result.destination &&
      result.source &&
      result.destination.index !== result.source.index
    ) {
      const newListFields = [...listFields];
      const [deletedField] = newListFields.splice(result.source.index, 1);
      newListFields.splice(result.destination.index, 0, deletedField);
      setListFields(newListFields);
      onChange(WidgetPath.FormManageFields, { fields: newListFields }, widgetName);
    }
  }

  const isShowForm = modalType !== '';
  const formTitle = modalType === 'new' ? 'New Field' : 'Edit Field';
  const listFieldsDraggable = listFields.slice(0, listFields.length - 1);
  const submitField = listFields[listFields.length - 1];

  return (
    <div>
      <WidgetSectionTitle title="Form Elements" />
      <DragDropContext onDragEnd={onDragEnd}>
        <StyledCollapse>
          <Panel header="Manage Fields" key="manageFields">
            <Droppable droppableId="all-fields" direction="vertical" type="fields">
              {(provided, snapshot) => {
                return (
                  <div {...provided.droppableProps} ref={provided.innerRef}>
                    {listFieldsDraggable.map((field, index) => {
                      const labelOrValue = field.label;
                      const IconField = MapIconFields[field.type] || <SettingOutlined />;

                      return (
                        <Draggable
                          key={String(field.attributes.name)} //
                          draggableId={String(field.attributes.name)}
                          index={index}
                          isDragDisabled={field.type === 'submit'}
                        >
                          {(provided, snapshot) => {
                            return (
                              <StyledField
                                {...provided.draggableProps} //
                                ref={provided.innerRef}
                                key={String(field.attributes.name)}
                              >
                                <div className="field-draggable">
                                  <MenuOutlined className="icon-to-drag" {...provided.dragHandleProps} />
                                </div>
                                <div className="field-area">
                                  <Input addonBefore={IconField} value={labelOrValue} onChange={onChangeLabel(field)} />
                                  <div className="field-area-bottom">
                                    <span className="field-name">{field.attributes.name}</span>
                                    {field.type !== 'submit' && (
                                      <div className="field-actions">
                                        <span className="field-action-edit" onClick={onClickEditField(field)}>
                                          Edit
                                        </span>
                                        <span className="field-action-delete" onClick={onClickDeleteField(field)}>
                                          Delete
                                        </span>
                                      </div>
                                    )}
                                  </div>
                                </div>
                              </StyledField>
                            );
                          }}
                        </Draggable>
                      );
                    })}
                    {provided.placeholder}
                  </div>
                );
              }}
            </Droppable>
            <StyledField key={String(submitField.attributes.name)}>
              <div className="field-draggable"></div>
              <div className="field-area">
                <Input
                  addonBefore={MapIconFields.submit}
                  value={String(submitField.attributes.value)}
                  onChange={onChangeLabel(submitField)}
                />
                <div className="field-area-bottom">
                  <span className="field-name">{submitField.attributes.name}</span>
                </div>
              </div>
            </StyledField>
            <Button
              size="large" //
              type="primary"
              block
              className="btn-add-field"
              onClick={() => {
                setFormData(defaultFormData);
                setModalType('new');
              }}
            >
              Add new field
            </Button>
          </Panel>
        </StyledCollapse>
      </DragDropContext>
      {isShowForm && (
        <Modal
          visible={isShowForm} //
          title={formTitle}
          footer={null}
          onCancel={() => setModalType('')}
        >
          <Form onFinish={onFinishForm} initialValues={formData} labelCol={{ span: 7 }} wrapperCol={{ span: 14 }}>
            {/* Label */}
            <Form.Item label="Label" name="label">
              <Input />
            </Form.Item>

            {/* Form Type */}
            <Form.Item label="Type" name="type">
              <Select>
                <Select.Option value="text">Text</Select.Option>
                <Select.Option value="email">Email</Select.Option>
                <Select.Option value="number">Number</Select.Option>
                <Select.Option value="checkbox">Checkbox</Select.Option>
              </Select>
            </Form.Item>

            {/* Attribute Name */}
            <Form.Item
              label="Attribute Name" //
              name="attrName"
              rules={[
                { required: true, message: 'This field is required' },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    const attrName = value as string;
                    if (attrName.includes(' ')) {
                      return Promise.reject(new Error('The attribute name cannot contain spaces'));
                    }

                    if (attrName === formData.attrName) {
                      return Promise.resolve();
                    }

                    const found = listFields.find((f) => f.attributes.name === attrName);
                    if (found) {
                      return Promise.reject(new Error('The attribute name need to be unique'));
                    }

                    return Promise.resolve();
                  },
                }),
              ]}
            >
              <Input />
            </Form.Item>

            {/* Attribute Required */}
            <Form.Item name="attrRequired" label="Required" valuePropName="checked">
              <Switch />
            </Form.Item>

            {/* Submit Form */}
            <Form.Item wrapperCol={{ offset: 7, span: 14 }}>
              <Button type="primary" htmlType="submit">
                Submit
              </Button>
            </Form.Item>
          </Form>
        </Modal>
      )}
    </div>
  );
}

export { WidgetFormManageFields };
