import React, { useEffect, useRef, useState } from 'react';
import { Switch, Row, Checkbox } from 'antd';
import type { CheckboxChangeEvent } from 'antd/es/checkbox';
import { WidgetProps, WidgetPath } from '../index';
import { WidgetWrap } from '../index.styled';
import { WidgetAlignComputed } from '../widgetAlignComputed';
import { WidthSlider } from '../widgetContentAreaWidth';
import { StyledWidthWrap } from './index.styled';

// const supportedAlign = ['left', 'right', 'center'];
// const supportedWidth = ['auto', 'fixedwidth', 'fullwidthOnMobile'];

type WidgetValueProp = {
  class: string;
  width: string;
};

type WidgetValueState = {
  align: string; // left | center | right
  width: number; // 20% -> 100%
  fullWidthOnMobile: boolean;
  autoWidth: boolean;
};

const defaultValueProps: WidgetValueProp = {
  class: 'center autowidth',
  width: '100%',
};
const defaultValueState: WidgetValueState = {
  align: 'center',
  width: 100,
  autoWidth: false,
  fullWidthOnMobile: false,
};

function transformFromValueProps(value?: WidgetValueProp): WidgetValueState {
  if (!value) {
    return defaultValueState;
  }

  const classStr = value.class;
  const widthNum = typeof value.width !== 'undefined' ? parseInt(value.width) : 100; // Percentage

  const classes = classStr.split(' ');

  return {
    align: classes.includes('center') //
      ? 'center'
      : classes.includes('left')
      ? 'left'
      : classes.includes('right')
      ? 'right'
      : 'center',
    width: widthNum <= 100 && widthNum >= 20 ? widthNum : 100,
    autoWidth: classes.includes('autowidth'),
    fullWidthOnMobile: classes.includes('fullwidthOnMobile'),
  };
}

function transformToValueProps(value: WidgetValueState): WidgetValueProp {
  const classes = [
    value.align, //
    value.autoWidth ? 'autowidth' : 'fixedwidth',
    value.fullWidthOnMobile ? 'fullwidthOnMobile' : undefined,
  ].filter(Boolean);

  return {
    class: classes.join(' '),
    width: `${value.width}%`,
  };
}

function WidgetImageSizeAlign({ value: valueProp, initValue, onChange }: WidgetProps<WidgetValueProp>) {
  const debounceRef = useRef<NodeJS.Timeout>();
  const [value, setValue] = useState(() => {
    return transformFromValueProps(initValue || valueProp || defaultValueProps);
  });

  useEffect(() => {
    if (valueProp) {
      setValue(transformFromValueProps(valueProp));
    }
  }, [valueProp]);

  const _onChange = (newValue: WidgetValueState, isSet: boolean = true) => {
    isSet && setValue(newValue);
    onChange(WidgetPath.ImageSizeAlign, transformToValueProps(newValue));
  };

  const onChangeAutoWidth = (newChecked: boolean) => {
    const newValue: WidgetValueState = {
      ...value,
      width: newChecked ? 100 : value.width,
      autoWidth: newChecked,
    };
    _onChange(newValue);
  };

  const onChangeAlign = (_: string, newAlign: string) => {
    const newValue: WidgetValueState = {
      ...value,
      align: newAlign,
    };
    _onChange(newValue);
  };

  const onChangeWidth = (newWidth: number) => {
    const newValue: WidgetValueState = {
      ...value,
      width: newWidth,
    };
    setValue(newValue);

    if (debounceRef.current) {
      clearTimeout(debounceRef.current);
      debounceRef.current = undefined;
    }

    debounceRef.current = setTimeout(() => {
      _onChange(newValue, false);
    }, 300);
  };

  const onChangeFullWidthMobile = (evt: CheckboxChangeEvent) => {
    const newChecked = evt.target.checked;
    const newValue: WidgetValueState = {
      ...value,
      fullWidthOnMobile: newChecked,
    };

    _onChange(newValue);
  };

  return (
    <StyledWidthWrap>
      <WidgetWrap>
        <label>Auto width</label>
        <Switch size="small" checked={value.autoWidth} onChange={onChangeAutoWidth} />
        <Row className={`slider-width-area ${value.autoWidth ? 'disabled' : ''}`}>
          <WidthSlider
            className="slider-width" //
            value={value.width}
            min={20}
            max={100}
            step={1}
            onChange={onChangeWidth}
            disabled={value.autoWidth}
          />
          <span className="slider-width-value">{value.width}%</span>
        </Row>
        <div className="full-w-mobile">
          <Checkbox
            checked={value.fullWidthOnMobile} //
            onChange={onChangeFullWidthMobile}
          >
            Full width on mobile
          </Checkbox>
        </div>
      </WidgetWrap>
      <WidgetAlignComputed name="" value={value.align} onChange={onChangeAlign} />
    </StyledWidthWrap>
  );
}

export { WidgetImageSizeAlign };
